<template>
  <form class="card" @submit.prevent="submit">
    <div class="card-header">
      <h5 class="card-title">Endereço da empresa</h5>
    </div>
    <div class="card-body">
      <div class="form-group">
        <label for="ipt-cep">CEP</label>
        <the-mask mask="#####-###" id="ipt-cep" type="tel" class="form-control" required v-model="cep" @focusout.native="consultarCep"></the-mask>
      </div>
      <div class="form-group">
        <label for="ipt-uf">Estado (UF)</label>
        <the-mask mask="AA" id="ipt-uf" type="text" class="form-control" required v-model="uf"></the-mask>
      </div>
      <div class="form-group">
        <label for="ipt-cidade">Cidade</label>
        <input id="ipt-cidade" type="text" class="form-control" maxlength="40" required v-model="cidade">
      </div>
      <div class="form-group">
        <label for="ipt-bairro">Bairro</label>
        <input id="ipt-bairro" type="text" class="form-control" maxlength="50" required v-model="bairro">
      </div>
      <div class="form-group">
        <label for="ipt-logradouro">Endereço/Logradouro</label>
        <input id="ipt-logradouro" type="text" class="form-control" maxlength="60" required v-model="logradouro">
      </div>
      <div class="form-group">
        <label for="ipt-numero">Número</label>
        <input id="ipt-numero" type="text" class="form-control" maxlength="6" required v-model="numero">
      </div>
      <div class="form-group">
        <label for="ipt-complemento">Complemento</label>
        <input id="ipt-complemento" type="text" class="form-control" maxlength="30" v-model="complemento">
      </div>
    </div>
    <div class="card-footer text-center">
      <button type="button" class="btn btn-primary" @click="$router.push({ name: 'recadpj-1', params: {empresa: empresa} })">Voltar</button>
      <button type="submit" class="btn btn-success">Avançar</button>
    </div>
  </form>
</template>

<script>
  export default {
    name: "PjEndereco",
    props: {
      empresa: { required: true, type: Object },
      value: { default: () => ({}), type: Object }
    },
    data: () => ({
      cep: '',
      uf: '',
      cidade: '',
      bairro: '',
      logradouro: '',
      numero: '',
      complemento: ''
    }),
    methods: {
      submit() {
        const dados = this.value;
        dados.endereco = {
          cep: this.cep,
          uf: this.uf,
          cidade: this.cidade,
          bairro: this.bairro,
          logradouro: this.logradouro,
          numero: this.numero,
          complemento: this.complemento
        };
        this.$emit('input', dados);
        this.$router.push({ name: 'recadpj-3', params: {empresa: this.empresa} });
      },
      //Rotina interna
      async consultarCep() {
        try {
          if (this.cep.length !== 8) throw 'Comprimento do CEP é inválido';
          this.$store.commit('setLoading', 'Consultando CEP');
          const res = await this.$http.axios.get("https://api.postmon.com.br/v1/cep/" + this.cep);
          this.uf = res.data.estado;
          this.cidade = res.data.cidade;
          this.bairro = res.data.bairro;
          this.logradouro = res.data.logradouro;
        } catch (e) {
          // eslint-disable-next-line no-console
          console.log(e);
        } finally {
          setTimeout(() => this.$store.commit('setLoading', false), 600);
        }
      },
      popularCampos(e) {
        //Obtem os dados de forma inicial
        if (e.endereco) {
          this.cep        = e.endereco.cep ? e.endereco.cep.trim().replace(/\D/g,'') : '';
          this.uf         = e.endereco.uf ? e.endereco.uf.trim().toUpperCase() : '';
          this.cidade     = e.endereco.cidade ? e.endereco.cidade.trim().toUpperCase() : '';
          this.bairro     = e.endereco.bairro ? e.endereco.bairro.trim().toUpperCase() : '';
          this.logradouro = e.endereco.logradouro ? e.endereco.logradouro.trim().toUpperCase() : '';
          this.numero     = '';
          this.complemento= e.endereco.complemento ? e.endereco.complemento.trim().toUpperCase() : '';
        }

        //Obtem os dados de forma cacheada (caso eles ja fossem preenchidos anteriormente)
        const dados = this.value;
        if (dados.endereco) {
          this.cep = dados.endereco.cep;
          this.uf  = dados.endereco.uf;
          this.cidade = dados.endereco.cidade;
          this.bairro = dados.endereco.bairro;
          this.logradouro = dados.endereco.logradouro;
          this.numero = dados.endereco.numero;
          this.complemento = dados.endereco.complemento;
        }
      },
    },
    mounted() {
      this.popularCampos(this.empresa);
    },
    beforeRouteLeave(to, from, next) {
      to.params.empresa = this.empresa;
      next();
    }
  }
</script>

